import React, { useState } from 'react';


function Calculate() {

  function showVal(ev) {
    ev.preventDefault();

    let inps = document.querySelector(".inp");
    let val = inps.value;
    try {
      console.log(eval(val));
      inps.value = eval(val);

    } catch(er) {
      alert(er.message);
    }
  }

  function addFunc(e) {
    let inps = document.querySelector(".inp");
    inps.value += e.target.dataset.num;
    e.target.style.background = "black";
    e.target.style.color = "white";
    setTimeout(() => {
      e.target.style.background = "whitesmoke";
      e.target.style.color = "black";
    },200);
  }

  function deleteOne() {
    let inps = document.querySelector(".inp");

    let arr = [...inps.value];
    arr.pop();
    inps.value = arr.join("");
  }

  function deleteAll() {
    let inps = document.querySelector(".inp");

    inps.value = "";
  }

  return (
    <div className="container">
      <h1> SIMPLE CALCULATOR </h1>
      <form className="form1" onSubmit={showVal}>
        <input type="text" placeholder="0" className="inp">

        </input>
      </form>
      <div className="num_cont">
        <button type="button" value="c" id="btn" onClick={deleteAll} className='all'>
          C
        </button>
        <button type="button" value="del" id="btn" onClick={deleteOne} className='one'>
          del
        </button>
        <button type="button" value="%" id="btn" onClick={addFunc} data-num="%">
          %
        </button>
        <button type="button" value="/" id="btn" onClick={addFunc} data-num="/">
          /
        </button>
        <button type="button" value="7" id="btn" onClick={addFunc} data-num="7">
          7
        </button>
        <button type="button" value="8" id="btn" onClick={addFunc} data-num="8">
          8
        </button>
        <button type="button" value="9" id="btn" onClick={addFunc} data-num="9">
          9
        </button>
        <button type="button" value="x" id="btn" onClick={addFunc} data-num="*">
          x
        </button>
        <button type="button" value="4" id="btn" onClick={addFunc} data-num="4">
          4
        </button>
        <button type="button" value="5" id="btn" onClick={addFunc} data-num="5">
          5
        </button>
        <button type="button" value="6" id="btn" onClick={addFunc} data-num="6">
          6
        </button>
        <button type="button" value="-" id="btn" onClick={addFunc} data-num="-">
          -
        </button>
        <button type="button" value="1" id="btn" onClick={addFunc} data-num="1">
          1
        </button>
        <button type="button" value="2" id="btn" onClick={addFunc} data-num="2">
          2
        </button>
        <button type="button" value="3" id="btn" onClick={addFunc} data-num="4">
          3
        </button>
        <button type="button" value="+" id="btn" onClick={addFunc} data-num="+">
          +
        </button>
        <button type="button" value="**" id="btn" onClick={addFunc} data-num="**">
          **
        </button>
        <button type="button" value="0" id="btn" onClick={addFunc} data-num="0">
          0
        </button>
        <button type="button" value="." id="btn" onClick={addFunc} data-num=".">
          .
        </button>
        <button type="button" value="=" id="btn" onClick={showVal}>
          =
        </button>
      </div>
    </div>
  );
}

export default Calculate;
